import { Popover, Space, Table, Tag } from "antd"
import Text from "antd/lib/typography/Text"
import { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { VList } from 'virtual-table-ant-design'
import Search from "antd/lib/input/Search"
import { useHotkeys } from 'react-hotkeys-hook'
import { InfoCircleOutlined } from '@ant-design/icons';
import { getItemsAsync, isLoading, itemList } from "./itemSlice"

let lastIndex = 0
const updateIndex = () => {
  lastIndex++
  return lastIndex
}

export default function Quest() {
  const o_items = useSelector(itemList)
  const [items, setItems] = useState(o_items)
  const loading = useSelector(isLoading)
  const dispatch = useDispatch()
  const searchInput = useRef()

  const columns = [
    {
      title: '아이템',
      key: `item${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <Text>{r.ko}</Text>
        <Text type="secondary">{r.jp}</Text>
      </Space>,
    },
    {
      title: '레어도',
      key: `rare${updateIndex()}`,
      dataIndex: 'rare'
    },
    {
      title: '최대 소지 수',
      key: `max${updateIndex()}`,
      dataIndex: 'max'
    },
    {
      title: '판매가',
      key: `sell${updateIndex()}`,
      dataIndex: 'sell'
    },
    {
      title: '구매가',
      key: `buy${updateIndex()}`,
      dataIndex: 'buy'
    }
  ]

  useEffect(() => {
    dispatch(getItemsAsync())
  }, [dispatch])

  useEffect(() => {
    setItems(o_items)
  }, [o_items])

  const onSearch = (value) => {
    setItems(o_items.filter(v => v.ko.includes(value)))
  }
  useHotkeys('ctrl+f', (e) => {
    searchInput.current.focus({
      cursor: 'all',
    })
    e.preventDefault();  
  });
  const onKeyDownSearch = (e) => {
    if (e.keyCode === 114 || (e.ctrlKey && e.keyCode === 70)) {        
        e.preventDefault();         
        searchInput.current.focus({
          cursor: 'all',
        })   
    }    
  }

  return (
    <>
      <Space style={{ marginBottom: 16 }}>
        <Search placeholder="아이템 명 검색" 
          allowClear onSearch={onSearch} style={{ width: 400 }} 
          onKeyDown={onKeyDownSearch}
          ref={searchInput}/>
      </Space>
      <Table columns={columns} dataSource={items} pagination={false} 
      loading={loading}
      rowKey='id' 
      scrollToFirstRowOnChange={true}
      scroll={{ y: '80vh' }}
      components={VList({
        height: 1000,
      })}/>
    </>
  )
}