import _ from 'lodash';
import { getItems, getQuestLv, getQuestQuery, getQuestRewards } from './const_query';
const initSqlJs = require('sql.js');
let _db = undefined;

async function getDB() {
  if (_db !== undefined) return _db
  const sqlPromise = initSqlJs({
    locateFile: file => `/${file}`
  });
  const dataPromise = fetch("/mhxx.db").then(res => res.arrayBuffer());
  const [SQL, buf] = await Promise.all([sqlPromise, dataPromise])
  _db = new SQL.Database(new Uint8Array(buf));

  return _db
}

export function getQuestsFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let quests = {}
    db.each(getQuestQuery,
        (r) => { quests[r.id] = r },
        () => {
          let rewards = []
          db.each(getQuestRewards, 
              (r) => rewards.push(r), () => {
                let nr = _.reduce(rewards, (p, c) => {
                  var qid = p[c.id] = p[c.id] || {A: [], B: [], C: [], D: []}
                  var type = qid[c.type] = qid[c.type] || []

                  type.push(_.omit(c, 'id'))

                  return p
                }, {})
                
                let result = _.chain(quests).mapValues(v => {
                  v.rewards = _.zipWith(
                                _.chain(nr[v.id].A).map(v => _.mapKeys(v, (_, k) => k + '_A')).value(), 
                                _.chain(nr[v.id].B).map(v => _.mapKeys(v, (_, k) => k + '_B')).value(), 
                                _.chain(nr[v.id].C).map(v => _.mapKeys(v, (_, k) => k + '_C')).value(), 
                                (a,b,c) => {
                                  return _.merge(a,b,c)
                                })
                  return v
                }).values().value()

                resolve(result)
              })
        })
    
  })
}

export function getQuestLvFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let lvs = []
    db.each(getQuestLv,
      (r) => lvs.push(r.lv), 
      () => resolve(lvs))
  })
}

export function getItemsFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let items = []
    db.each(getItems, 
      (r) => items.push(r), 
      () => resolve(items))
  })
}