import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { getItemsFromDB } from '../../app/db';

const initialState = {
  items: [],
  loading: false
}

export const getItemsAsync = createAsyncThunk(
  'quests/getQuest',
  async () => {
    const quests = await getItemsFromDB()

    return quests
  }
)

export const itemSlice = createSlice({
  name: 'item',
  initialState,
  reducers: {
    getItems: (state) => {
      state.items = []
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(getItemsAsync.pending, (state) => {
        state.loading = true;
      })
      .addCase(getItemsAsync.fulfilled, (state, action) => {
        state.items = action.payload
        state.loading = false
      })
  },
})

export const { getItems }  = itemSlice.actions

export const itemList = (state) => state.item.items
export const isLoading = (state) => state.item.loading

export default itemSlice.reducer